.basicInfo {
  padding: 5px;

  .contentPart {
    padding: 10px;
    margin: auto;
    height: 65vh;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
  }

  .contentPart::-webkit-scrollbar {
    width: 0.3em;
  }

  .contentPart::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(91, 90, 90, 0.2);
    border-radius: 5%;
  }

  .contentPart::-webkit-scrollbar-thumb {
    background-color: rgb(209, 204, 204);
    outline: 0.2px solid rgb(183, 186, 189);
    border-radius: 5%;
  }

  hr {
    border-top: 2px solid #e8e9e9;
  }

  .btn {
    column-gap: 17px;
  }

  .rowWidth {
    width: 270px;
    position: relative;
    margin-bottom: 8px;
  }

  .AddressWidth {
    width: 540px;
    position: relative;
  }

  .btnWidth {
    width: 160px;
  }

  .propertyLabel {
    color: #090909;
  }
}

.tooltipdesc {
  max-width: 475px !important;
  background-color: #ffffff;
  color: #1e2528;
  font-family: Barlow;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
}

.previewTextBtn {
  color: #935ed6;
  text-decoration: none;
}
.moreBtn {
  font-family: Barlow;
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
  color: #935ed6;
  text-decoration: none;
}
.moreBtn:hover {
  font-family: Barlow;
  font-size: 14px;
  line-height: 20px;
  font-weight: 600;
  color: #391765;
  text-decoration: underline;
}
.tableWrap {
  border: 1px solid#E8E9E9;
  border-radius: 9px;
  overflow: hidden;
  margin-top: 16px;
  width: max-content;
}
.table {
  width: 722px;

  thead {
    tr {
      th {
        background-color: #e2f8fb;
        color: #2a5067;
        font-family: Inter;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        padding: 14px 16px;
      }
    }
  }
  tbody {
    tr {
      td {
        padding: 8px 16px;
        vertical-align: bottom;
        border-bottom: 0 !important;
      }
    }
  }
}
.tableWrap tbody tr:nth-child(odd) {
  background-color: #fff;
} 
.tableWrap tbody tr:nth-child(even) {
  background-color: #f7f7f7;
  
}

.residentList {
  background: #ffffff;
  ul {
    margin-bottom: 0;
    padding-left: 12px;
    li {
      font-family: Barlow;
      font-weight: 600;
      font-size: 12px;
      line-height: 18px;
    }
    li:not(:last-child) {
      margin-bottom: 2px;
    }
  }
}

